import $ from 'jquery';
import store from 'store';
import vs from '../vs/vs.all';
import config from '../config';
import * as Theme from '../vs/theme';
import { setEditorValueAndRun, clearEditorStore, getEditorValue } from '../vs/editor'
import layout from '../components/layout.vue';
import penSettingDialog from '../components/penSettingDialog.vue';
import Dialog from 'dialog';
import noty from 'noty';

const title = "";

const serverBasePath = "/flow/form";

//
const convertMode = store.get("vs-convertMode") || "1";

//
const theme = Theme.getTheme();

//
const imgExt = store.get("vs-imgExt") || "png";

//class name
var fatherClassName = store.get("vs-fatherClassName") || "brick";

//class name
var sonClassName = store.get("vs-sonClassName") || "div";

//
var fontSize = config.editor.fontSize;

//
var components = {
    "layout": layout,
    "penSettingDialog": penSettingDialog
};

//
var data = {
    version: '',
    convertMode: convertMode,
    theme: theme,
    imgExt: imgExt,
    fatherClassName: fatherClassName,
    sonClassName: sonClassName,
    Title: title,
    systems:[],
    CategoryItemsAll: [],
    CategoryItems: [],
    LabelItems: [],
    LabelItemsAll: [],

    Html: "",
    Css: "",
    Js: "",

    isShowBgMask:false,
    isShowSettingBox: false,
    isShowViewBox: false,
    layoutType: "ly1",
    fontSize: fontSize,

    sn:'',
    categoryId:'',
    formName:'',
    sys: '',
    basePath:'',
    portalPath: '',
    sessionId:'',
    id:'',
    isReadOnly: true,

    //
    isActive: '',
    dataSources: [],
    ifShow: false,
    dataDetaile: {
        id: '',
        title: '',
        className: '',
        method: '',
        version: '',
        remark: '',
        methodType:1,
        sys:''
    },
    requestData: '',
    requestResult: '',
    interRow: ''
};

//
var methods = {
//
    getQueryString:function(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]); return null;
    },
    //
    runByCache: function () {
    },

    //
    downloadImg: function () {
      downloadImg();
    },

    //
    runEditor: function () {
        vs.doRunByEditor({}, true);
    },

    //
    settingsPen: function () {
    },

    //
    savePen: function () {
    },

    //
    newPen: function () {
    },

    //
    clearPen: function () {
        store.set("vs-title", "");
        clearEditorStore();
    },

    //
    setCategory: function () {
    },

    //
    setLabel: function () {
    },

    //
    quickAddCss: function () {
    },

    //
    quickAddJs: function () {
    },

    hideSettingBox: function () {
        this.isShowBgMask = false;
        this.isShowSettingBox = false;
    },

    hideViewBox: function () {
        this.isShowBgMask = false;
        this.isShowViewBox =false;
    },

    showSettingBox: function () {
        this.isShowBgMask = true;
        this.isShowViewBox = true;
    },

    //
    setting: function () {
        this.isShowSettingBox = true;
    },

    //
    setttingBoxHide: function () {
        this.isShowSettingBox = false;
    },

    testCreate: function () {
    },

    getEditors:function () {
        return $(".web-editor:visible .vs-editor");
    },

    validate:function(){
        let _this = this, truth = true;
        if(!this.dataDetaile.title){
            noty({
                text: '',
                type: "error",
                timeout: 2000
            });
            truth = false;
        }else if(!this.dataDetaile.className){
            noty({
                text: '',
                type: "error",
                timeout: 2000
            });
            truth = false;
        }else if(!this.dataDetaile.method){
            noty({
                text: '',
                type: "error",
                timeout: 2000
            });
            truth = false;
        }else if(!this.dataDetaile.version){
            noty({
                text: '',
                type: "error",
                timeout: 2000
            });
            truth = false;
        }
        return truth;
    },

    // 
    saveData:function(flag, datas){
        var _this = this;
        /*SN.html,css,js*/
        var saveObj = {
            isPublish:flag,
            status:flag,
            id:this.id,
            name:_this.formName,
            formSn:_this.sn,
            systemSn:_this.sys,
            categoryId:_this.categoryId,
            versionNo:_this.version,
            htmlContent:datas.html,
            cssContent:datas.css,
            jsContent:datas.javascript
        };
        $.ajax({
            url:this.basePath+serverBasePath+'/biz_form_info/save', data:saveObj, async:false, type:'POST', dataType:'JSON',
            success:function(dt){
                if(dt.code === '100'){
                    try{
                        parent.showSucTips(dt.msg);
                    }catch(e){
                        alert(dt.msg);
                    }
                    if(dt.data){
                        _this.id = dt.data.id;
                    }
                    parent.doSetBaseData(dt.data);
                }else{
                    try{
                        parent.showErrTips(dt.msg);
                    }catch(e){
                        alert(dt.msg);
                    }
                }

            }, error:function () {
                try{
                    parent.showErrTips('');
                }catch(e){
                    alert(dt.msg);
                }
            }
        });
    },

    //
    saveCode: function () {
        /*if(!_this.validate()){
            return;
        }*/
        let valData = getEditorValue();
        this.saveData(1, valData);
    },

    //
    publishCode:function () {
        let _this = this;
        /*if(!_this.validate()){
            return;
        }*/
        _this.saveData(2, getEditorValue());
        return;
        let pubDialog = Dialog({
            title: "",
            content: $("#dialog-confirm"),
            padding: "20px 30px ",
            className: "dialog-settings",
            showClassName:"dialog-show",
            resizable: false,
            height: "auto",
            width: 400,
            modal: true,
            ok: function(){
                /*SN.html,css,js*/
                _this.saveData(1, getEditorValue());
            },
            close: function () {
                pubDialog.hide();
                return false;
            }
        });
    },

    //
    initDataSource () {
        let _this = this;
        let initDataUrl = this.basePath+serverBasePath+'/biz_form_data/ajaxList?sessionId='+this.sessionId+'&formSn='+this.sn;
        $.ajax({ url:initDataUrl, data:{}, async: false, type: 'POST', dataType: 'JSON',
            success:function (response) {
                _this.dataSources = response.datas;
            }
        });
    },

    //
    interAdd () {
        //
        Object.keys(this.dataDetaile).forEach(key => this.dataDetaile[key]= '');
        //
        this.ifShow = true;
    },

    //
    interSave () {
        //
        if(!this.validate()){
            try{
                parent.showWarnTips('');
            }catch(e){
                alert('');
            }
            return;
        }
        let newObj = Object.assign({}, this.dataDetaile, );
        //
        let saveInterUrl = this.basePath+serverBasePath+'/biz_form_data/save?sessionId='+this.sessionId+'&formSn='+this.sn;
        let saveInterObj = newObj;
            //
            //this.dataSources.push(newObj);
        $.ajax({ url:saveInterUrl, data:saveInterObj, async:false, type:'POST', dataType:'JSON',
            success:function (response) {
                if(response.code == 100){
                    try{
                        parent.showSucTips('');
                    }catch(e){
                        alert('');
                    }
                }else{
                    try{
                        parent.showErrTips(response.msg);
                    }catch(e){
                        alert(response.msg);
                    }
                }
            }, error:function () {
                try{
                    parent.showErrTips('');
                }catch(e){
                    alert('');
                }
            }
        });
        //
        this.initDataSource();
        //
        Object.keys(this.dataDetaile).forEach(key => this.dataDetaile[key]= '');
    },

    //
    showInterDetail (dataList, index) {
        this.isActive = index;
        let _this = this.dataDetaile;
        _this.id = dataList.id;
        _this.title = dataList.title;
        _this.className = dataList.className;
        _this.method = dataList.method;
        _this.version = dataList.version;
        _this.remark = dataList.remark;
        _this.methodType = 1;
        this.ifShow = true;
    },

    //
    interTest (row) {
        this.interRow = row;
        this.requestData = '';
        this.requestResult = '';
        let interDialog = Dialog({
            title: "",
            content: $("#dialog-interTest"),
            padding: "20px",
            className: "dialog-settings",
            showClassName:"dialog-show",
            resizable: false,
            height: "auto",
            width: 600,
            modal: false,
            lock:false,
            parent:window,
            close: function () {
                interDialog.hide();
                return false;
            }
        });
    },

    //
    interTestBtn () {
        let _this = this;
        let testUrl = this.portalPath + '/common/doApiData.jhtml';
        let datas = {
            id: this.dataSources[this.interRow].id,
            className: this.dataSources[this.interRow].className,
            method: this.dataSources[this.interRow].method,
            version: this.dataSources[this.interRow].version,
            params: this.requestData
        };
        $.ajax({ url:testUrl, data:datas, async:false, type:'POST', dataType:'JSONP', jsonp : "jsoncallback",
            success:function (response) {
                console.info(response);
                _this.requestResult = JSON.stringify(response);
                //return;
                if(response.code == 100){
                    try{
                        parent.showSucTips(response.msg);
                    }catch(e){
                        alert(response.msg);
                    }
                    _this.requestResult = response.data;
                }else{
                    try{
                        parent.showErrTips(response.msg);
                    }catch(e){
                        alert(response.msg);
                    }
                }
            },error:function (XMLHttpRequest) {
                try{
                    parent.showErrTips(XMLHttpRequest.status+' '+'');
                }catch(e){
                    alert(XMLHttpRequest.status+' '+'');
                }
            }
        });
    },

    //
    interDel (row) {
        let _this = this;
        let interDialog = Dialog({
            title: "",
            content: $("#dialog-interDel"),
            padding: "20px",
            className: "dialog-settings",
            showClassName:"dialog-show",
            resizable: false,
            height: "auto",
            width: 300,
            modal: true,
            ok: function () {
                let delDataUrl = _this.basePath+serverBasePath+'/biz_form_data/dels?sessionId='+_this.sessionId+'&ids='+_this.dataSources[row].id;
                $.ajax({ url:delDataUrl, data:{}, async: false, type: 'POST', dataType: 'JSON',
                    success:function (response) {
                        if(response.code == 100){
                            try{
                                parent.showSucTips(response.msg);
                            }catch(e){
                                alert(response.msg);
                            }
                        }else{
                            try{
                                parent.showErrTips(response.msg);
                            }catch(e){
                                alert(response.msg);
                            }
                        }
                        //
                        _this.initDataSource();
                    }
                });
                //
                //_this.dataSources.splice(row, 1);
            },
            close: function () {
                interDialog.hide();
                return false;
            }
        });
    }
};

//vue
var mounted = function () {
    vs.indexVue = this;
    vs.indexVueData = this._data;
    vs.init();
};

//
var watch = {

    convertMode: function (val, oval) {
        store.set("vs-convertMode", val)
    },

    Title: function (val, oval) {
        store.set("vs-title", val)

    },
    fatherClassName: function (val, oval) {
        store.set("vs-fatherClassName", val);
    },

    sonClassName: function (val, oval) {
        store.set("vs-sonClassName", val);
    },
    imgExt: function (val, oval) {
        store.set("vs-imgExt", val);
    },

    theme: function (val,oval) {
        Theme.setTheme(val,oval)
    },

    fontSize: function (val, oval) {
   
        Theme.setFontSize(val)
    }
};

//
var beforeCreate = function () {

    Theme.setConfigByStore();
};
module.exports = {

    data() {
        return data;
    },

    methods: methods,

    watch: watch,

    mounted: function () {
        mounted.call(this);
        var _this = this;
        //IDSN
        if(this.id == '' || this.id == undefined) {
            this.isReadOnly = false;
        }

        $.ajax({
            url:this.basePath + '/basedata/flow_system/getAll',
            async:true, type:'POST', dataType:'JSON', success:function (response) {
                if(response.code === '100'){
                    _this.systems = response.data;
                }
            }
        });

        //
        var url = this.basePath+serverBasePath+'/biz_form_info/getBizFormInfoBySn?sessionId='+this.sessionId+'&sn='+this.sn;
        $.ajax({url:url, data:{}, async:true,type:'POST', dataType:'JSON', success:function(response){
                if(response.code == '100'){
                    let dt = response.data;
                    _this.htmlContent = dt.htmlContent;
                    _this.sn = dt.formSn;
                    _this.id = dt.id;
                    _this.formName = dt.name;
                    _this.version = dt.versionNo;
                    _this.sys = dt.systemSn;
                    setTimeout(function(){
                        // clearEditorStore();
                        setEditorValueAndRun({html:dt.htmlContent, css:dt.cssContent, javascript:dt.jsContent});
                    }, 1000);
                }
            }, error:function (XMLHttpRequest) {
                try{
                    parent.showErrTips(XMLHttpRequest.status+' '+'');
                }catch(e){
                    alert(XMLHttpRequest.status+' '+'');
                }
            }
        });


    },

    //
    beforeCreate: function () {
        beforeCreate.call(this);
    },

    created: function () {
        var _this = this;
        this.basePath = this.getQueryString('basePath');
        this.portalPath = this.getQueryString('portalPath');
        this.sessionId = this.getQueryString('sessionId');
        this.id = this.getQueryString('id');
        this.sn = this.getQueryString('sn');
        this.categoryId = this.getQueryString('categoryId');

        //'saveCode''ctrl+s'
        document.onkeydown=function (e) {
            e=window.event||e;
            var key=e.keyCode;
            if(key== 83 && e.ctrlKey){
                /*FF  */
                setTimeout(function(){
                    // alert('ctrl+s');
                    _this.saveCode();
                },1);
                return false;
            }
        };
        //
        this.initDataSource();
    },

    components: components
};


